// Fields

body.adminimal-skin-material {

  // Title field
  &.page-node-edit,&.page-node-add {
    .form-item-title {
      label {
        display: none;
      }
      input {
        font-size: 30px;
        padding: 10px 15px;
        font-weight: 300;
        &::placeholder {
          color: #c75051;
        }
        margin: 20px 0;
      }
    }
  }

  // URL field.
  .link-field-column {
    float: none;
    width: auto;
    margin-bottom: 7px;
    label {
      font-size: 14px;
      display: inline;
      font-weight: 500;
    }
    input {
      width: auto;
      margin-left: 10px;
    }
    .description {
      display: none;
    }
  }
}
